/*login*/

body{
    background: #fbe1d3 url("../images/login_bg.jpg") center top no-repeat;
    width: 100%;
    height: 100%;
}
.container {
  width: 100%;
  min-width: 1200px;
}
.content {
  width: 1200px;
  margin: 0 auto;
}
.login .loginCon {
	width: 300px;
	height: 330px;
	padding: 20px 30px;
	margin-top: 100px;
	margin-right: 30px;
	float: right;
	box-shadow: 0px 0px 5px #666;
	border-radius: 10px;
	background: #fff;
	
}
.login .loginCon h3{
	font-size: 20px;
	
}
.login .loginCon h3 span a{

	float: right;
	font-size: 14px;
	color: #02b1cb;
	
}
.login .loginCon .info {
	margin-top: 40px;
}
.login .loginCon .info p{
	font-size: 14px;
}
.login .loginCon .info  lable{
	margin-right: 10px;
}
.login .loginCon .info input.txt{
	width: 240px;
	height: 35px;
	margin-top: 20px;
}
.login .loginCon .info input.checkbox{
	margin-left: 120px;
	margin-top:10px;
}
.login .loginCon .info  input.checkbox span{
	display: inline-block;
	margin-left: 5px;
}
.login .loginCon .info input.btn{
    width: 245px;
    height: 35px;
    margin-top: 20px;
    margin-left: 41px;
    color: #fff;
    background: #ed145b;
    font-size: 16px;
    text-align: center;
    border: none;
    border-radius: 6px;
}
.login .loginCon .info  a{
	color: #02b1cb;
	margin-top: 20px;
	font-size: 12px;
}
.login .loginCon .info  a.forgot{
	float: left;
	margin-left: 43px;
}
.login .loginCon .info  a.getpwd{
	float: right;
	margin-right: 17px;
}

/*register*/

#register .login .loginCon{
	height: 450px;
	width: 320px;
}
#register .login .loginCon .info lable{
	line-height: 75px;
}
#register .login .loginCon input.txt{
	float: right;
	
}
#register .login .loginCon .info input.btn{
	margin-left: 79px;
}
#register .login .loginCon .info  a.forgot{
	margin-left: 85px;
}
